import { TextField , Button, RadioGroup, FormControlLabel, Radio} from '@material-ui/core'
import React,{useState, useEffect} from 'react'
import {useHistory } from "react-router-dom";
import ApiService from "./ApiServiceLogin";
import styled from "styled-components";

function LoginTextComponent() {
    
    const history = useHistory(); 

    const[user, setUser] = useState({user_email:"", user_password:"" , user_role:"0"}); //user 
    const [isBtnActive, setIsBtnActive] = useState(false); //Button  , useeffect() 
    

    //     
    useEffect(() => {
    setIsBtnActive(
        user.user_email.includes("@") && user.user_password.length >=1 ? true : false
    );
    }, [user]);

    //input   user   
    const inputHandler = (e) => { 
        e.persist();
        const{ name, value} = e.target;
        setUser({...user,
                [name]: value});
    };

    // 
    const getLogin = () => { 
       if(!isBtnActive){
           window.alert("  .");
       }else{
        ApiService.postLogin(user)
        .then(res => {
            if(user.user_email=== res.data.user_email){        
            let user_email = res.data.user_email;
            let message = user_email+' ';
            console.log(":",message);
            console.log("getLogin() ");
            //user_email 
            sessionStorage.setItem("user",user_email);
            //user_role    
            if(res.data.user_role==="0"){
            //history  (   )
            history.replace("/");
            window.location.reload();
            }else{
                history.push("/signIn");
            }
            } else{
                window.alert("   .");
                history.push('/signIn');
            }
        })  
        .catch( err => {
            console.log('getLogin() ', err);
        });
    }
    }

    const Header = styled.div`
    margin: 0 0 20px 0;
    `;

    const Footer = styled.div`
    margin: 30px 0;
    align-items: left;
    `;
 
    return (
       <div className='loginForm'style={{width: '350px',
                    margin:'80px auto',
                    height: '400px',
                    maxHeight: '500px',}}>            
            <div className='login-header'>
            <h3></h3>
            <Button onClick={()=>history.push("/signUp")} size="large" color="primary"> SignUp</Button>
            </div>

            <form style={{margin: '30px auto',
                         borderTop: '1px solid lightGray',
                         padding: '10px 0',}}>
            <div style={{ margin :'10px 0', }}>
            <TextField type="email" label="/" placeholder=" ." fullWidth margin="normal"
            name="user_email"  value={user.user_email || ""} onChange={inputHandler} variant="filled" size="small"/>
            <br/>
            <TextField type="password" label="" placeholder=" ."  fullWidth margin="normal"
            name="user_password" value={user.user_password || ""} onChange={inputHandler} variant="filled" size="small"/>
            <br/>
           </div>
            <div>
           <RadioGroup row aria-label="row" value={user.user_role} name="user_role" onChange={inputHandler}>
                <FormControlLabel value="0" control={<Radio color="default" size="small"/>} label="" />
                <FormControlLabel value="1" control={<Radio color="default" size="small"/>} label="" />
            </RadioGroup>        
           </div>
           <Button fullWidth variant="contained" onClick={getLogin}></Button><br></br>
          
           </form>        
           <Footer>
            <Button onClick={() => {history.push("/findEmail")}}> </Button>&nbsp;&nbsp;
            <Button onClick={() => {history.push("/findPW")}}> </Button>
            </Footer>      
        </div>

 
    );

}
const Header = styled.div`
margin: 0 0 20px 0;
`;

const Footer = styled.div`
margin: 30px 0;
align-items: left;
`;

export default LoginTextComponent
