import { TextField , Button} from '@material-ui/core'
import React,{useState, useEffect} from 'react'
import {useHistory} from "react-router-dom";
import styled from "styled-components";

import ApiService from "./ApiServiceLogin";
import Footer from "./FindIdFooter";

function FindIDComponent() {
     const history = useHistory(); 

     const[user, setUser] = useState({user_name:"", user_birthday:""}); //user 
     const [isBtnActive, setIsBtnActive] = useState(false); // useeffect() 

     useEffect(() => {
        setIsBtnActive(
            user.user_name.length>1 && user.user_birthday.length === 8 ? true : false
        );
        }, [user]);
 
     //input   user   
     const inputHandler = (e) => { 
         e.persist();
         const{ name, value} = e.target;
         setUser({...user,
                 [name]: value});
     };

     const getID = () => { 
        if(!isBtnActive){
            window.alert("  .");
        }else{
         ApiService.findEmail(user)
         .then(res => {
             console.log(res.data);
             let find_users = res.data;
            if(user.user_name == find_users[0]?.user_name){
                console.log("getID() ");
                
                window.sessionStorage.setItem("find_user",JSON.stringify(find_users));  
                history.push('/Email');
            } else{
                window.alert("    .");
               window.location.reload();
            }
         })
         .catch( err => {
             console.log('getID() ', err);
         });
     }
    }

    //  
 const onKeyPress = (e) => {
    if (e.key === "Enter") {
        getID();
    }
}

    const Header = styled.div`
    font-size: 14px;
    `;

    return (
        <>        
        <div style={{ width: '350px',
                     margin:'80px auto',
                     height: '420px',
                     maxHeight: '500px',}}>
            <h3> </h3>
            <Header>
            <p>COS   .<br/>
                   <br/>.</p>
            </Header>
            <form style={{margin: '30px auto',
                         borderTop: '1px solid lightGray',
                         padding: '10px 0',}}>
           <div style={{ margin :'10px 0', }}>
            <TextField type="text"  label="" placeholder="  ." onKeyPress={onKeyPress}
            name="user_name" fullWidth margin="dense" value={user.user_name|| ""} onChange={inputHandler}/>
            <TextField type="text"  label="" placeholder="  ." onKeyPress={onKeyPress}
            name="user_birthday" fullWidth margin="normal" value={user.user_birthday|| ""} onChange={inputHandler}
            helperText="  -  (ex:20010101)"/>
            </div>
           <Button fullWidth variant="contained" onClick={getID}></Button><br></br>
         </form> 
         <Footer/>  
         </div>
        </>
    )
}

export default FindIDComponent
