import React from 'react';
import {Button, makeStyles, Modal, Backdrop, Fade} from '@material-ui/core';

function ModalDeleteUserComponent(props) {

    // 
    const useStyles = makeStyles((theme) => ({
        modal: {
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        },
        paper: {
          backgroundColor: theme.palette.background.paper,
          boxShadow: theme.shadows[5],
          padding: theme.spacing(5, 4, 3),
          borderRadius:'10px'
        },
      }));

    const classes = useStyles();

    return (
        <div>
            {/*    */}
            <Modal
                className={classes.modal}
                open={props.openUserDelete}
                onClose={props.handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={props.openUserDelete}>
                    <div className={classes.paper} style={{width:'640px'}}>
                        <h5> </h5>
                        <div style={{fontSize:'12px', textAlign:'left'}}>
                          .<br/>
                               .<br/>
                          ,     .<br/>
                        </div>

                        <hr style={{height:'1px', backgroundColor:'lightgray', border:'0px', opacity:'70%', margin:'50px 0px 50px 0px', paddingBottom:'0px'}}/>
                        <h5> </h5>
                        <div style={{fontSize:'12px', textAlign:'left'}}>
                            .<br/>
                           , ,     ,    .<br/>
                        <br/>
                            .<br/>
                          ID     .<br/>
                        <br/>
                                 .<br/>
                                  .       .<br/>
                        (,     )
                        <hr style={{height:'1px', backgroundColor:'lightgray', border:'0px', opacity:'70%', margin:'50px 0px 50px 0px', paddingBottom:'0px'}}/>

                          ,       .        .<br/>
                        </div>
                        <div style={{marginTop:'50px', marginBottom:'50px'}}></div>
                        <div style={{display:'flex', justifyContent:'center'}}>
                            <button variant="contained" style={{backgroundColor:'white', width:'200px', height:'40px', borderRadius:'0px', marginBottom:'10px', boxShadow:'none', fontSize:'13px', border:'1px solid lightgray', marginRight:'10px'}} onClick={props.handleClose}></button>
                            <button variant="contained" style={{backgroundColor:'#444', width:'200px', height:'40px', borderRadius:'0px', marginBottom:'10px', boxShadow:'none', fontSize:'13px', color:'white', border:'0px'}} name='recheck_user_delete' onClick={props.handleOpen}> </button><br/>
                        </div>
                    </div>
                </Fade>
            </Modal>

            <Modal
                className={classes.modal}
                open={props.openReCheckUserDelete}
                onClose={props.handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                        <Fade in={props.openReCheckUserDelete}>
                            <div className={classes.paper}>
                                <h5>   ?</h5>
                                    <Button variant="contained" style={{backgroundColor:'white', width:'200px', height:'40px', borderRadius:'0px', marginBottom:'10px', boxShadow:'none', fontSize:'13px', border:'1px solid lightgray', marginRight:'10px'}} onClick={props.handleClose}></Button>
                                    <Button variant="contained" style={{backgroundColor:'#444', width:'200px', height:'40px', borderRadius:'0px', marginBottom:'10px', boxShadow:'none', fontSize:'13px', color:'white'}} name='recheck_user_delete' onClick={props.deleteUser}> </Button><br/>
                            </div>
                            </Fade>
                    </Modal>       
        </div>
    )
}


export default ModalDeleteUserComponent
