import {Table, TableRow, TableCell, TableHead} from '@material-ui/core'
import Footer from "./FindPwFooter";
import styled from "styled-components";
import React,{ useEffect} from 'react';


function ShowPassword() {

    let user_name = sessionStorage.getItem("user_name");
    let user_birthday = sessionStorage.getItem("user_birthday");
    let user_email = sessionStorage.getItem("user_email");
    let user_password = sessionStorage.getItem("user_password");

    useEffect(()=>{
        console.log('  ');
        return ()=>{
            sessionStorage.clear();
            console.log('sessionStorage clear()');
        };
    },[]);

    
    return (
        <Div style={{textAlign:'center', minHeight:'110%'}}>
               <div className="PW-header">
                <h3>  </h3>    
                <p>    .</p>                                    
            </div>
            <div className="PW-body">
            <Table style={{marginBottom:'20px', marginTop:'40px'}}> 
                <TableRow>
                    <TableCell align="center"></TableCell>
                    <TableCell align="center">{user_name}</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell align="center"></TableCell>
                    <TableCell align="center">{user_birthday}</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell align="center">/</TableCell>
                    <TableCell align="center">{user_email}</TableCell>
                    </TableRow>
                <TableRow>
                    <TableCell align="center"></TableCell>
                    <TableCell align="center">{user_password}</TableCell>
                    </TableRow>
            </Table>
            <p style={{fontSize:'14px', color:'gray', textAlign:'left'}}>        .</p>
            </div>
            <Footer/>
        </Div>
    )
}

const Div = styled.div`
    width: 350px;
    margin:80px auto;
    height: auto;
    maxHeight: 700px;
    `;

export default ShowPassword

