import React from 'react'
function ManagerSidebar({productInsertOpen,productListOpen,userListOpen,userStatusOpen,orderStatusOpen,cancleOrderOpen}) {
    return (
        <div className="manager-sidebar">
                <ul>
                    <butten className='sidebarBtn' onClick={()=>productInsertOpen()}><li></li></butten>
                    <butten className='sidebarBtn' onClick={()=>productListOpen()}><li></li></butten>
                    <butten className='sidebarBtn' onClick={()=>orderStatusOpen()}><li></li></butten>
                    <butten className='sidebarBtn' onClick={()=>cancleOrderOpen()}><li></li></butten>
                    <butten className='sidebarBtn' onClick={()=>userListOpen()}><li></li></butten>
                    <butten className='sidebarBtn' onClick={()=>userStatusOpen()}><li></li></butten>
                </ul>
        </div>
    )
}

export default ManagerSidebar
