import React, { useState } from 'react';
import AxiosApiService from "../../AxiosApiService";
import DaumPostcode from 'react-daum-postcode';
import '../css/signUp.css';
import { makeStyles } from '@material-ui/core/styles';
import Banner from '../../Maincomponent/Banner';
import { Modal, Backdrop, Fade } from '@material-ui/core';
import SignUpBox from './SignUpBox';
import { useHistory } from "react-router-dom";

function SignUp(props) {
    let history = useHistory();
    const [user_email, setUser_email] = useState('');            //
    const [user_password, setUser_password] = useState('');      // 
    const [user_passwordChk, setUser_passwordChk] = useState('');// 
    const [passwordError, setPasswordError] = useState(false);   //  
    const [user_name, setUser_name] = useState('');              //
    const [user_birthday, setUser_birthday] = useState('');      //
    const [user_gender, setUser_gender] = useState('');        //
    const [user_phone, setUser_phone] = useState('');            //
    const [post_code, setPost_code] = useState();                  //
    const [address, setAddress] = useState('');                  //
    const [detail_address, setDetail_address] = useState('');      //
    const [isOpenPost, setIsOpenPost] = useState(false);        //api
    const [userCheck, setUserCheck] = useState(0); //   
    const [emailCheck, setEmailCheck] = useState(false); //   
    const [openDetailAddress, setOpenDetailAddress] = useState(false);

    const toggleNav = (e) => {
        e.preventDefault();
        //  falue true true falue 
        setIsOpenPost(isOpenPost => !isOpenPost);
        console.log(isOpenPost);
    }

    function checkEmail(e) {  // 
        e.preventDefault();
        if (/^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/.test(user_email)) {
            AxiosApiService.getUserEmail(user_email)
                .then(res => {
                    let userEmail = res.data;//     
                    // setUserCheck(userEmail);  //  useState 
                    if (userEmail > 0) {          //  0   
                        alert('  ')
                    } else {
                        alert('   .')
                        setEmailCheck(true);
                    }
                })
                .catch(err => {
                    console.log('getUserEmail() ', err);
                });
        } else {
            alert('  .')
        }

    }

    // submit  
    function saveUser(e) {
        e.preventDefault();

        //
        if (!(/^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/.test(user_email)) || user_email === '' || emailCheck === false) {
            alert(' ');
            return false;
        }
        if (!(/^(?=.*\d)(?=.*[a-zA-Z])[0-9a-zA-Z]{8,10}$/.test(user_password)) || user_password === '') {
            alert('   .');
            return false;
        }
        if (user_passwordChk === '') {
            alert('  ');
            return false;
        }
        if (user_name === '') {
            alert(' ');
            return false;
        }
        if (!(/^(19[0-9][0-9]|20\d{2})(0[0-9]|1[0-2])(0[1-9]|[1-2][0-9]|3[0-1])$/.test(user_birthday)) || user_birthday === '') {
            alert(' ');
            return false;
        }
        if (!(/^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-?[0-9]{3,4}-?[0-9]{4}$/.test(user_phone)) || user_phone === '') {
            alert(' ');
            return false;
        }
        if (post_code === '') {
            alert(' ');
            return false;
        }
        if (address === '') {
            alert(' ');
            return false;
        }
        if(detail_address===''){
            alert(' ');
            return false;
        }
       
        //    
        let user = {
            user_email: user_email,
            user_password: user_password,
            user_name: user_name,
            user_birthday: user_birthday,
            user_gender: user_gender,
            user_phone: user_phone,
            post_code: post_code,
            address: address,
            detail_address: detail_address
        }
        console.log(user);
        //     axios
        AxiosApiService.insertUser(user)
            .then(res => {
                alert('.');
                history.push('/'); // 
            })
            .catch(err => {
                history.push('/signUpError');
                console.log('saveUser() ', err);
            });

    }

    //    
    function onEmail(e) {
        setUser_email(e.currentTarget.value)
    }
    function onPassword(e) {
        setUser_password(e.currentTarget.value)
    }
    function onPasswordChk(e) {
        //      
        setPasswordError(e.target.value !== user_password);
        setUser_passwordChk(e.currentTarget.value);
    }
    function onName(e) {
        setUser_name(e.currentTarget.value)
    }
    function onGender(e) {
        setUser_gender(e.currentTarget.value)
    }
    function onBirthday(e) {
        setUser_birthday(e.currentTarget.value)
    }
    function onPhone(e) {
        setUser_phone(e.currentTarget.value)
    }
    function onPostcode(e) {
        setPost_code(e.currentTarget.value)
    }
    function onAddress(e) {
        setAddress(e.currentTarget.value)
    }

    function onDetailAddress(e) {
        setDetail_address(e.currentTarget.value)
    }

    const useStyles = makeStyles((theme) => ({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            boxShadow: theme.shadows[5],
            padding: theme.spacing(5, 5, 5),
            borderRadius: '10px'
        },
    }));

    const classes = useStyles();

    const handleClose = (e) => {
        setIsOpenPost(false);
        setOpenDetailAddress(false);
    };

    //  api
    function seachAddress(e) {
        const onCompletePost = (data) => {
            let fullAddress = data.address;
            let zonecode = data.zonecode;
            let extraAddress = '';

            if (data.addressType === 'R') {
                if (data.bname !== '') {
                    extraAddress += data.bname;
                }
                if (data.buildingName !== '') {
                    extraAddress += (extraAddress !== '' ? `, ${data.buildingName}` : data.buildingName);
                }
                fullAddress += (extraAddress !== '' ? ` (${extraAddress})` : '');
            }

            setPost_code(zonecode);
            setAddress(fullAddress);
            setIsOpenPost(false);
        }

        const postCodeStyle = makeStyles((theme) => ({
            paper: {
                position: 'absolute',
                width: 400,
                backgroundColor: theme.palette.background.paper,
                border: '2px solid #000',
                boxShadow: theme.shadows[5],
                padding: theme.spacing(2, 4, 3),
            },
        }));
        return (
            <Modal
                className={classes.modal}
                open={isOpenPost}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={isOpenPost}>
                    <div className={classes.paper} style={{ width: '640px' }}>
                        {isOpenPost && <DaumPostcode style={postCodeStyle} onComplete={onCompletePost} />}
                    </div>
                </Fade>
            </Modal>
        );
    }


    return (
        <>
            <Banner />
            <SignUpBox
                checkEmail={checkEmail}
                onEmail={onEmail}
                onPassword={onPassword}
                onPasswordChk={onPasswordChk}
                onGender={onGender}
                onName={onName}
                onBirthday={onBirthday}
                onPhone={onPhone}
                toggleNav={toggleNav}
                onPostcode={onPostcode}
                onDetailAddress={onDetailAddress}
                saveUser={saveUser}
                passwordError={passwordError}
                seachAddress={seachAddress}
                onAddress={onAddress}
                onDetailAddress={onDetailAddress}
                isOpenPost={isOpenPost}
                post_code={post_code}
                address={address}
            />
        </>
    )
}

export default SignUp;
