
import { TextField , Button, RadioGroup, FormControlLabel, Radio} from '@material-ui/core'
import React,{useState, useEffect} from 'react'
import {useHistory } from "react-router-dom";
import ApiService from "./ApiServiceLogin";
import styled from "styled-components";
import "./css/ModalLoginForm.css";

function ModalLoginForm(props) {
    
 // , ,     
 const { open, close } = props;

 const history = useHistory(); 

 const[user, setUser] = useState({user_email:"", user_password:"" , user_role:"0"}); //user 
 const [isBtnActive, setIsBtnActive] = useState(false); //Button  , useeffect() 
 

 //     
 useEffect(() => {
 setIsBtnActive(
     user.user_email.includes("@") && user.user_password.length >=1 ? true : false
 );
 }, [user]);

 //input   user   
 const inputHandler = (e) => { 
     e.persist();
     const{ name, value} = e.target;
     setUser({...user,
             [name]: value});
 };

 // 
 const getLogin = () => { 
    if(!isBtnActive){
        window.alert("  .");
    }else{
     ApiService.postLogin(user)
     .then(res => {
         if(user.user_email=== res.data.user_email){        
         let user_email = res.data.user_email;
         let message = user_email+' ';
         console.log(":",message);
         console.log("getLogin() ");
         //user_email 
         sessionStorage.setItem("user",user_email);
         window.location.reload();
         //user_role    
          if(res.data.user_role==="1"){
            sessionStorage.setItem("user_role",1);
          }
         } else{
             window.alert("   .");
         }
     })  
     .catch( err => {
         console.log('getLogin() ', err);
     });
 }
 }

 //  
 const onKeyPress = (e) => {
    if (e.key === "Enter") {
        getLogin();
    }
}


 const [openedModal, setOpendModal] = useState(open);

 const closeModal = ()=>{
    setOpendModal(close);
 }

 const Header = styled.div`
 margin: 0 0 20px 0;
 `;

 const Footer = styled.div`
 margin: 30px 20px;
 align-items: left;
 `;

function joinuser(){
    history.push("/signUp"); 
    closeModal();
}


 return (
     //   openModal  .
     <div className={ open ? 'openModal modal' : 'modal' }>
         { open ? (  
             <section>
                 <header>
                     <span> <h3 style={{color:'gray'}}>/ Sign In</h3></span>
                     <button className="close" onClick={close}> &times; </button>
                 </header>
                 <main> 
            <Button onClick={()=>joinuser()} style={{float:'right', backgroundColor:'white', width:'100px', height:'30px', boxShadow:'none', fontSize:'13px'}}><h3>SignUp</h3></Button>
                 <form style={{margin: '30px auto'}}>
                     <div style={{ margin :'10px 0', }}>
                              <TextField type="email" label="/" placeholder=" ." onKeyPress={onKeyPress} fullWidth margin="normal"
                                 name="user_email"  value={user.user_email || ""} onChange={inputHandler}  size="small"/>
                             <br/>
         <TextField type="password" label="" placeholder=" ."  onKeyPress={onKeyPress} fullWidth margin="normal"
         name="user_password" value={user.user_password || ""} onChange={inputHandler} size="small"/>
         <br/>
        </div>
         <div>
        <RadioGroup row aria-label="row" style={{marginBottom:'10px', justifyContent:'center'}}value={user.user_role} name="user_role" onChange={inputHandler}>
             <FormControlLabel value="0" control={<Radio color="default" size="small"/>} label="" />
             <FormControlLabel value="1" control={<Radio color="default" size="small"/>} label="" />
         </RadioGroup>        
        </div>
        <Button fullWidth variant="contained" style={{backgroundColor:'#444', height:'40px', borderRadius:'0px', marginBottom:'10px', boxShadow:'none', fontSize:'13px', color:'white'}} onClick={getLogin}></Button><br></br>
       
        </form>
                 </main>
                 <Footer>
                 <Button onClick={() => {history.push("/findEmail"); closeModal()}}> </Button>&nbsp;&nbsp;
                 <Button onClick={() => {history.push("/findPW"); closeModal()}}> </Button>
                 </Footer>
             </section>
         ) : null }
     </div>
 )
}

export default ModalLoginForm
