import React, {useState} from 'react';
import {Button} from '@material-ui/core';
import ApiService from '../../ApiService';

function InsertUserAccount(props) {

    const [user_account, setuser_account] = useState(null);
    const [user_bank, setuser_bank] = useState(null);
    const [user_name, setuser_name] = useState(props.user_name);
    const [user_email, setuser_email] = useState(props.user_email);



    function insertUserAccount(){
        const userAccount = {
            user_account : user_account,
            user_bank : user_bank,
            user_name : user_name,
            user_email : user_email,
        }
        // console.log(userAccount);

        ApiService.insertUserAccount(userAccount);
        alert('   .')
        props.handleClose();
    }

    const onChangeAccount = (e) =>{
        setuser_account(e.target.value);
    }

    const onChangeBank = (e) =>{
        setuser_bank(e.target.value);
    }

    return (
        <div>
            <div style={{textAlign:'left', marginTop:'20px'}}>
                <div style={{fontSize:'11px', color:'gray', textAlign:'left', marginBottom:'5px'}}></div>
                <input style={{textAlign:'left', border:'0px', borderRadius:'0px', padding:'8px', fontSize:'11px', color:'gray', backgroundColor:'#E7E7E7', width:'285px', marginBottom:'10px'}} type="text" value={props.user_name} disabled/><br/>
            </div>

            <div style={{textAlign:'left'}}>
                <div style={{fontSize:'11px', color:'gray', textAlign:'left', marginBottom:'5px'}}></div>
                <select name="user_bank" style={{textAlign:'left', border:'0px', borderRadius:'0px', padding:'8px', fontSize:'11px', color:'gray', backgroundColor:'#E7E7E7', width:'300px', marginBottom:'10px'}} onChange={onChangeBank}>
                    <option> </option>
                    <option value="NH">NH </option>
                    <option value="KB">KB </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="IBK">IBK </option>
                    <option value=""></option>
                    <option value=""></option>
                </select>
            </div>

            <div style={{textAlign:'left'}}>
                <div style={{fontSize:'11px', color:'gray', textAlign:'left', marginBottom:'5px'}}></div>
                <input style={{textAlign:'left', border:'0px', borderRadius:'0px', padding:'8px', fontSize:'11px', color:'gray', backgroundColor:'#E7E7E7', width:'170px', marginBottom:'10px'}} type="text" placeholder="'-'    ." onChange={onChangeAccount}/>
                <Button style={{backgroundColor:'#444', width:'105px', height:'30px', borderRadius:'0px', marginLeft:'10px',marginBottom:'0px', boxShadow:'none', fontSize:'13px', color:'white'}} onClick={() => insertUserAccount()}></Button>
            </div>
        </div>
    )
}


export default InsertUserAccount
