import React, { useState, useRef } from 'react';
import '../managerCss/productinsert.css'
import CancelIcon from '@material-ui/icons/Cancel';
function ProductInsert({ productInsertOptionOpen,saveImg }) {
    const [product_title, setProduct_title] = useState('');           //
    const [product_gender, setProduct_gender] = useState('');         //
    const [product_category, setProduct_category] = useState('');     //
    const [product_price, setProduct_price] = useState(0);             //
    const [product_content, setProduct_content] = useState('');       //
    const [imgPriView, setImgPriView] = useState([]);
    const [imgStr, setImgStr] = useState([]);
    const [product_img, setProduct_img] = useState([{img:0}]);             //
    const [product_material, setProduct_material] = useState();
    const [sidebarOpen, setsidebarOpen] = useState(true);
    const imageInput = useRef();
    
    const sidebarIsOpen = () => {
        setsidebarOpen(true);
    }
    function onTitle(e) {
        setProduct_title(e.currentTarget.value);
    }
    function selectGender(e) {
        setProduct_gender(e.currentTarget.value);
    }
    function selectCategory(e) {
        setProduct_category(e.currentTarget.value);
    }
    function onPrice(e) {
        setProduct_price(e.currentTarget.value);
    }
    function onContent(e) {
        setProduct_content(e.currentTarget.value);
    }
    function onMaterial(e) {
        setProduct_material(e.currentTarget.value);
    }

    const ImgDelete = (e, priviews,index) => {
        setImgPriView(imgPriView.filter(priview=>priview.img!==priviews));
        setImgStr(imgStr.filter(str=>str!==imgStr[index]));
    }
    //  
    function onImg(e) {
        if (e.target.files.length > 0) {
            //0   imageFile 
            const imageFile = e.target.files[0];
            const imageUrl = URL.createObjectURL(imageFile);
            // 
            setProduct_img(product_img.concat(e.target.files[0]));
            // 
            setImgPriView(imgPriView.concat([{ img: imageUrl }]));
            setImgStr(imgStr.concat(imageFile.name));
        }
    }
    // input [type=file]  
    const fileClick = () => {
        imageInput.current.click();
    }

    function optionPage(e) {
        e.preventDefault();
        //    
        saveImg(product_img);
        let product = {
            product_title: product_title,
            product_gender: product_gender,
            product_category: product_category,
            product_price: product_price,
            product_content: product_content,
            //join  ,  
            product_img: imgStr.join(','),
            product_material: product_material
        }
        //  String  JSON.stringify(object)
        window.localStorage.setItem('product', JSON.stringify(product));
        productInsertOptionOpen();
    }

    return (
        <>
            <h1 style={{ marginTop: '30px' }}></h1>
            <div className="sticky">
            <div className="img-category">
                {sidebarIsOpen &&
                    <div className="priview_sidebar">
                    {imgPriView.map((priview,index)=>
                          <img className="priview_sidebar_img" src={priview.img}/>
                    )}
                    </div>
                }
                <div className="priview_img_box">
                    <div className="priview_img" >
                        {imgPriView.map((priview, index) =>
                            <>
                                <div className="priview_img_index">
                                    <button className="priview_button" onClick={(e) => ImgDelete(e, priview.img,index)}><CancelIcon /></button>
                                    <img className="priview" src={priview.img} />
                                </div>
                            </>
                        )}
                    </div>

                </div>
            </div>
            <div className='displaybox'>
                    <div className="title_box">
                        <label className="titleLabel"> </label>
                        <input className="product_title" name="product_title"
                            type="text" placeholder="" onChange={onTitle}></input>
                    </div>
                    <div className="select-box">
                        <div className="select-box-div">
                            <label className="selectLabel"></label>
                            <select className="select_category" onChange={selectGender} defaultValue="">
                                <option value="M"></option>
                                <option value="W"></option>
                                <option value="KIDS"></option>
                            </select>
                        </div>
                        <div className="select-box-div">
                            <label className="selectLabel"></label>
                            <select className="select_category" onChange={selectCategory} defaultValue="">
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>
                                <option value=""></option>

                            </select>
                        </div>
                    </div>

                    <div className="product_price_stock">
                        <label className="selectLabel"> </label>
                        <input className="product_price" name="product_price"
                            type="text" placeholder="" onChange={onPrice}></input>
                    </div>
                    <div className="product_price_material">
                        <label className="selectLabel"> </label>
                        <input className="product_price"
                            type="text" placeholder=" " onChange={onMaterial}></input>
                    </div>
                    <div className="content_box">
                        <label className="selectLabel"></label>
                        <div className="content_box_scroll">
                            <textarea type="text" name="product_content" className="product_content"
                                placeholder="" onChange={onContent}></textarea>
                        </div>
                    </div>
                    <div style={{ display: 'flex', width: '100%' }}>
                        <button className='optionPage' type="button" onClick={fileClick}> </button>
                        <button className='optionPage' type="button" onClick={optionPage}> </button>
                        <input ref={imageInput} style={{ display: 'none' }} multiple="multiple" name="product_img" type='file' onChange={onImg} />
                    </div>

                </div>
                </div>
        </>
    )
}
export default ProductInsert
